.header {
    height: 95vh;    
    background-image: linear-gradient(to right bottom,
    rgba($color-black, 0.808),
     rgba($color-white, 0.795)),
     url(../img/hero.jpg);
    background-size: cover;
    background-position: top;
    margin-bottom: 30px;
    position: relative;

    

    & .logo {
        height: 3.5rem; }

    & .logo-box {
        position: absolute;
        top: 4rem;
        left: 4rem; }
    
    & .text-box {
        position: absolute;
        top: 35%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center; }

        
        }

