body { 
    font-family: "Lato", sans-serif;
    font-weight: 400;
    line-height: 1.7;
    color: $color-grey-dark;
    padding: 3rem;
}

.h1-primary {
    color: #fff;
    text-transform: uppercase; 
  
    .heading-primary-main {
    display: block;
    font-weight: 800;
    font-size: 70px;
    letter-spacing: 35px;
    text-align: center;
    animation: moveInTop 5s;
    backface-visibility: hidden;
    margin-bottom: -20px; }
  
  .heading-primary-sub {
    display: block;
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 15px;
    animation: moveInBottom 5s;
    backface-visibility: hidden;
    margin-bottom: 10px; }
  
  }

  .heading-secondary { 
    font-size: 3.5rem;
    text-transform: uppercase;
    font-weight: 700;
    padding-bottom: 5rem;
    letter-spacing: .2rem;
    transition: all .5s;
    
    
  }

  .heading-tertiary { 
    font-size: $default-font-size;
    font-weight: 700;
    text-transform: uppercase;
    color: $color-grey-dark;
  }

  .heading-about-learnmore { 
    font-size: $default-font-size;
    font-weight: 700;
    text-transform: uppercase;
    color: $color-grey-dark;
  }

  .paragraph { 
    font-size: 2rem;
    color: $color-grey-dark;
    margin-bottom: 5rem;
    &:not(:last-child) { 
      margin-bottom: 5rem;
 
    }
  }

.paragraph-about-learnmore { 

  font-size: $default-font-size;
  color: $color-grey-dark;
   
  &:not(:last-child) { 
    margin-bottom: 3rem;
 
    }
  }


