.btn {
    &:link,
    &:visited {
      text-transform: uppercase;
      text-decoration: none;
      padding: 15px 40px;
      display: inline-block;
      border-radius: 20px;
      transition: all .2s;
      position: relative;
      animation: moveInButton 5s; }
    
    &:hover {
      transform: translateY(-3px);
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2); }
    
    &:active {
      transform: translateY(1px);
      box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2); }
    
    & -white {
      background-color: #fff;
      color: #777; }

      &-green {
        background-color: rgb(31, 105, 24);
        color: #777; }
    
    &::after {
      content: "";
      display: inline-block;
      height: 100%;
      width: 100%;
      border-radius: 20px;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      transition: all .4s; }
    
    &::after {
      background-color: #fff; }
    
    &:hover::after {
      opacity: 0; }
    
    & -animated {
      animation: moveInButton 5s ease-in-out; }
      
    }

    .btn-text { 
      &:link,
      &:visited { 
        font-size: $default-font-size;
        color: $color-midnight-grey;
        display: inline-block;
        text-decoration: none;
        border: 1px  solid $color-midnight-grey;
        padding: 3rem 6rem 3rem 6rem;
        border-radius: 5rem;
        transition: all .2s;
      }

      &:hover { 
        background-color: $color-midnight-grey;
        color: $color-white;
        box-shadow: 0 1rem 2rem rgba($color-black, .15);
        transform: translateY(-3px);
      }

      &:active { 
        box-shadow: 0 1rem 2rem rgba($color-black, .15);
        transform: translateY(3px);
 
      }
     
    }