.feature-box {
    background-color: rgba($color-white, .8);
    font-size: 1.5rem;
    padding: 2.5rem;
    text-align: center;
    border-radius: .3rem;
    box-shadow: 0 1.5rem 4rem rgba($color-black, .15); 
    transition: transform .3s;
    
        
    
    
    &__icon {
        font-size: 6rem;
        margin-bottom: .5rem;
        display: inline-block;
        background-image: linear-gradient(to bottom, $color-midnight-grey, $color-grey-light-1);
        -webkit-background-clip: text;
         color: transparent;
    }

    &:hover {
        transform: translateY(-.5rem) scale(1.08);
    }
}

