.card {

    //FUNCTIONALITY 
    perspective: 150rem;
    -moz-perspective: 150rem;
    position: relative;
    height: 70rem;
    
    &__side {
    height: 70rem;
    transition: all 1s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    backface-visibility: hidden;
    border-radius: 3px;
    overflow: hidden;
    box-shadow: 0 1.5rem 4rem rgba($color-black, .15);
    

        &--front {
            background-color: teal;
    
        }

        &--back {
            background-color: brown;
            transform: rotateY(180deg);

            &-1 {
                background-image: linear-gradient(to right bottom, $color-secondary-light-1, $color-secondary-light-2);
            }
        }
        
    }

    
    

    &:hover &__side--front {
        transform: rotateY(-180deg);
    }
    &:hover &__side--back {
        transform: rotateY(0);
    }

    //FRONT SIDE STYLING
    
    &__picture {
        background-size: cover;
        height: 23rem;
        background-blend-mode: hue;
        -webkit-clip-path: polygon(1% 1%, 0% 100%, 11% 86%, 21% 99%, 28% 86%, 38% 100%, 54% 91%, 61% 100%, 72% 81%, 80% 100%, 92% 91%, 97% 98%, 100% 1%);
clip-path: polygon(1% 1%, 0% 100%, 11% 86%, 21% 99%, 28% 86%, 38% 100%, 54% 91%, 61% 100%, 72% 81%, 80% 100%, 92% 91%, 97% 98%, 100% 1%);
        
        &--1 {
            background-image: linear-gradient(to right bottom, $color-secondary-light-1, $color-secondary-light-2),  url(../img/nat-5.jpg);
        }

        &--2 {
            background-image: url(../img/nat-5.jpg);
        }

        &--3 {
            background-image: url(../img/nat-5.jpg);
        }
    }

    &__heading {
        font-size: 2.8rem;
        font-weight: 300;
        text-transform: uppercase;
        text-align: right;
        color: $color-white;
        position: absolute;
        top: 10rem;
        right: 2rem;
        width: 75%;
        
        
    }

    &__heading-span {
        padding: 1rem 1.5rem;
        -webkit-box-decoration-break: clone;
        box-decoration-break: clone;
        &--1 {
            background-image: linear-gradient(to right bottom, 
            rgba($color-secondary-light-1, .85), 
            rgba($color-secondary-light-2, .85));
        }
    }

    &__details {
        padding: 3rem;
        
        ul {
            list-style: none;
            width: 80%;
            margin: 0 auto;
            
            li {
                text-align: center;
                font-size: 1.5rem;
                padding: 1rem;
                color: $color-white;

                &:not(:last-child) {
                    border-bottom: 1px solid $color-black;
                }

 
            }    
        }
    }

    //Front side styling
    &__cta {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%)
       
    }

    &__price-box {
        text-align: center;
        color: $color-white;
        
    }

    &__price-only {
        font-size: 1.4rem;
        text-transform: uppercase;
    }

    &__price-value {
        font-size: 5rem;
        font-weight: 100;
    }

}
